.mainPosts {
	padding-right: 15%;
	padding-left: 15%;
	padding-top: 5%;
	-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
	box-sizing: border-box;
	font-size: 1.3em;
}
.switchRule {
	position: relative;
	padding-bottom: 10%;
}
.switchRule > div {
	position: absolute;
	width: 100%;
	padding-bottom: inherit;
}
@media all and (max-width: 900px) {
	.mainPosts {
		padding-left: 5%;
		padding-right: 5%;
		max-width: 100%;
	}
}



