.header {
	height: 130px;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background-color: #2c2c43;
	font-size: 1.6em;
	text-align: center;
	-webkit-box-shadow: 1px 0px 9px #2c2c43;
	        box-shadow: 1px 0px 9px #2c2c43;
}
.App-title {
	margin-left: 15%;
	display: inline;
	white-space: nowrap;
	color: white;
}

@media all and (max-width: 900px) {
	/* css for mobile devices */
	.header {
		height: 80px;
	}
	.App-title {
		font-size: 0.5em;
		margin-left: 5%;
	}
}



